import React, { useState } from "react";
import "./userTd.sass";

export const TdCustomerPrice = (props) => {
  const [showTooltip, setShowTooltip] = useState(false);
  const handleMouseOver = (e) => {
    setShowTooltip(true);
  };
  const handleMouseLeave = (e) => {
    setShowTooltip(false);
  };
  return (
    <td
      className="userTd"
      onMouseOver={handleMouseOver}
      onMouseLeave={handleMouseLeave}
    >
      {props.elem.customerPayment != " "
        ? Number(props.elem.customerPrice)
        : Math.floor(
            (props.elem.customerPrice - props.elem.partialPaymentAmount) * 100
          ) / 100}
      {showTooltip && props.elem.customerPayment == " " && (
        <div className="userTdTooltip">
          <p className="userTdP">
               {props.elem.customerPrice} {" "}
          </p>
          <p className="userTdP">
             {props.elem.partialPaymentAmount} 
          </p>
          <p className="userTdP">
            {" "}
            {Math.floor(
              (props.elem.customerPrice - props.elem.partialPaymentAmount) * 100
            ) / 100}
             
          </p>
        </div>
      )}
      {showTooltip && props.elem.customerPayment == "  ." && (
        <div className="userTdTooltip">
          <p className="userTdP">
               {props.elem.partialPaymentAmount} {" "}
          </p>
          <p className="userTdP">
               {props.elem.customerPrice} 
          </p>
          <p className="userTdP">
              {" "}
            {Math.floor(
              (props.elem.partialPaymentAmount - props.elem.customerPrice) * 100
            ) / 100}
             
          </p>
        </div>
      )}
    </td>
  );
};
